import React from "react";
import {Input, Spin} from "antd"
import "antd/dist/antd.css"
import "../css/universal.css"
// 导入redux
import {useDispatch, useSelector} from "react-redux"
import {Change_Parameter_Mid} from "../../../store/modules/main_redux"
// 导入常量
import {CONTROL_MAIN_DAtA} from "../constant/data"
import {Switch_Train_fun} from "../constant/Func_Train"

interface TrainProps{
    theme_begin: any,
    // 全局界面宽
    Width_page: number,
}

interface TrainState{
    Input_x_str: string, // 输入的Train-X的数据
    Input_y_str: string, // 输入的Train-Y的数据
    Show_Loading: boolean, // 判断是否显示加载图标
}

function Train(props: TrainProps, state: TrainState){
    const {TextArea} = Input
    const {theme_begin, Width_page} = props
    const [Input_x_str, Set_Input_x_str] = React.useState("")
    const [Input_y_str, Set_Input_y_str] = React.useState("")
    const [Show_Loading, Set_Show_Loading] = React.useState(false)
    let theme = {
        ...theme_begin,
        width: theme_begin.width * Width_page,
        left: theme_begin.left * Width_page
    }
    let button_height = 60 // 训练模型的按钮所占的高度
    let text_height = 40 // 数据输入域名称
    // Train Model按钮的真实数据
    let button_height_true = 40
    let button_width_true = 0.7 * theme.width
    // redux
    const {Model_index, Algorithm_index, Parameter} = useSelector(state => (state as any).main_redux)
    const dispatch = useDispatch()


    /**
     * @Change_Input_x_str 修改Input_x_str里面的内容(Change_Input_y_str同上)
     */
    function Change_Input_x_str(e: any){
        Set_Input_x_str(e.target.value)
    }
    function Change_Input_y_str(e: any){
        Set_Input_y_str(e.target.value)
    } 

    React.useEffect(() => {
        if(Show_Loading){
            dispatch(Change_Parameter_Mid(Switch_Train_fun(Model_index, Algorithm_index, Parameter, Input_x_str, Input_y_str)))
            Set_Show_Loading(false)
        }
    }, [Show_Loading])


    return(
        <div className="Train" style={{...theme}}>
            {/* <div style={{float: 'left', width: theme.width - 4, height: theme.height - button_height}}> */}
            {/* 训练数据的Train-X */}
            <div style={{float: 'left', width: theme.width - 4 - 30, height: text_height, lineHeight: text_height.toString() + 'px'}}>&nbsp;Train-X:&nbsp;</div>
            <div className="MyButton" style={{float: 'left', width: 25, height: text_height - 15, marginTop: 15 / 2, fontSize: '24px', lineHeight: (text_height - 20).toString() + 'px'}}
                onClick={() => {
                    let str_list = CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]['reference-x'].split(":")
                    Set_Input_x_str(str_list[1].slice(1))
                }}
            >+</div>
            <TextArea
                style={{float: "left", width: theme.width - 4 - 10, marginLeft: 5, height: (theme.height - button_height) / 2 - text_height}}
                allowClear
                value={Input_x_str}
                placeholder={CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]['reference-x']}
                onChange={Change_Input_x_str}
            ></TextArea>
            <div style={{float: 'left', width: theme.width - 4 - 30, height: text_height, lineHeight: text_height.toString() + 'px'}}>&nbsp;Train-Y:&nbsp;</div>
            {/* 训练数据的Train-Y */}
            <div className="MyButton" style={{float: 'left', width: 25, height: text_height - 15, marginTop: 15 / 2, fontSize: '24px', lineHeight: (text_height - 20).toString() + 'px'}}
                onClick={() => {
                    let str_list = CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]['reference-y'].split(":")
                    Set_Input_y_str(str_list[1].slice(1))
                }}
            >+</div>
            <TextArea
                style={{float: "left", width: theme.width - 4 - 10, marginLeft: 5, height: (theme.height - button_height) / 2 - text_height}}
                allowClear
                value={Input_y_str}
                placeholder={CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]['reference-y']}
                onChange={Change_Input_y_str}
            ></TextArea>
            {/* 训练数据按钮 */}
            <div className="MyButton" style={{float: "left", fontSize: '16px', width: button_width_true, height: button_height_true, lineHeight: button_height_true.toString() + 'px', marginLeft: (theme.width - button_width_true) / 2, marginTop: (button_height - button_height_true) / 2}}
                onClick={() => {
                    Set_Show_Loading(true)
                }}
            >
                Train&nbsp;Model
            </div>
            {/* 加载框 */}
            <div style={{position: 'absolute', display: Show_Loading ? "inline" : "none", width: theme.width - 4, height: theme.height - 4, left: 0, top: 0, opacity: 0.8, background: 'white'}}>
                <Spin style={{float: 'left', marginTop: theme.width / 2 - 10, marginLeft: theme.height / 2 - 55}} tip="Loading..."></Spin>
            </div>
        </div>
    )
}

export default Train