import React from "react";
import {Input} from 'antd'
import "antd/dist/antd.css"
import "../css/universal.css"
// 导入常量
import {CONTROL_MAIN_DAtA} from "../constant/data"
import {Switch_Predict_fun} from "../constant/Func-Predict"
// 导入redux
import {useSelector} from "react-redux"

interface PredictProps{
    theme_begin: any,
    // 全局界面宽
    Width_page: number,
}

interface PredictState{
    Predict_X: string, // 预测数据的X
    Predict_Y: string, // 预测数据的Y
}

function Predict(props: PredictProps, state: PredictState){
    const {theme_begin, Width_page} = props
    const {TextArea} = Input
    const [Predict_X, Set_Predict_X] = React.useState("")
    const [Predict_Y, Set_Predict_Y] = React.useState("")
    let theme = {
        ...theme_begin,
        width: theme_begin.width * Width_page,
        left: theme_begin.left * Width_page
    }
    // 该组件的相关常量
    let button_height = 50 // 预测按钮所占的高度
    let text_height = 28 // 数据输入域名称
    let button_height_true = 30 // 按钮真实高度
    let button_width_true = 0.7 * theme.width // 真实宽度
    // redux
    const {Model_index, Algorithm_index, Parameter_Mid} = useSelector(state => (state as any).main_redux)


    /**
     * @Change_Predict_X 修改预测数据X的值
     */
    function Change_Predict_X(e: any){
        Set_Predict_X(e.target.value)
    }


    return(
        <div className="Predict" style={{...theme}}>
            <div style={{float: 'left', width: theme.width - 4 - 30, height: text_height, lineHeight: text_height.toString() + 'px'}}>&nbsp;Predict-X:&nbsp;</div>
            <div className="MyButton" style={{float: 'left', width: 25, height: text_height - 5, marginTop: 5 / 2, fontSize: '24px', lineHeight: (text_height - 10).toString() + 'px'}}
                onClick={() => {
                    let str_list = CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]['predict-x'].split("or")
                    Set_Predict_X(str_list[1].slice(1))
                }}
            >+</div>
            <TextArea
                style={{float: "left", width: theme.width - 4 - 10, marginLeft: 5, height: (theme.height - button_height) / 2 - text_height - 5}}
                allowClear
                value={Predict_X}
                placeholder={CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"][Algorithm_index]["predict-x"]}
                onChange={Change_Predict_X}
            ></TextArea>
            <div style={{float: 'left', width: theme.width - 4, height: button_height - 4, borderBottom: "2px dashed rgb(180, 180, 180)"}}>
                <div className="MyButton" style={{float: "left", fontSize: '16px', width: button_width_true, height: button_height_true, lineHeight: button_height_true.toString() + 'px', marginLeft: (theme.width - button_width_true) / 2, marginTop: (button_height - button_height_true) / 2 - 2}}
                    onClick={() => {
                        // 判断模型是否被训练了
                        if(Parameter_Mid.length === 0){
                            alert("You should train the model before forecasting!")
                        } else {
                            Set_Predict_Y(Switch_Predict_fun(Model_index, Algorithm_index, Parameter_Mid, Predict_X) as any)
                        }
                    }}
                >
                    Predict
                </div>
            </div>
            <div style={{float: 'left', width: theme.width - 4, height: text_height, lineHeight: text_height.toString() + 'px'}}>&nbsp;Predict-Y:&nbsp;</div>
            <TextArea
                style={{float: "left", width: theme.width - 4 - 10, marginLeft: 5, height: (theme.height - button_height) / 2 - text_height - 5}}
                allowClear
                value={Predict_Y}
            ></TextArea>
        </div>
    )
}

export default Predict