import React from "react";
// 导入redux
import {useDispatch} from 'react-redux'
import {change_Page_now} from "../../../store/modules/main_redux"
// 导入常量
import {PAGE_NAME} from "../../constant/data"

interface TitleProps{
    theme_begin: any,
    // 全局界面宽
    Width_page: number,
}

interface TitleState{
}

function Title(props: TitleProps, state: TitleState){
    const {theme_begin, Width_page} = props
    const dispatch = useDispatch()
    let theme = {
        ...theme_begin,
        width: theme_begin.width * Width_page,
        left: theme_begin.left * Width_page
    }
    return(
        <div className="Title" style={{...theme}}>
            <div style={{width: 60, float: 'right', height: theme.height - 5, lineHeight: (theme.height - 5).toString() + 'px', marginRight: 10, marginTop: 2.5, cursor: 'pointer', background: 'rgb(220, 220, 220)', borderRadius: '4px', textAlign: 'center'}}
                onClick={() => {
                    dispatch(change_Page_now(PAGE_NAME[1]))
                }}
                >Others</div>
        </div>
    )
}

export default Title