// 每个算法的训练函数(train model function)

// 从常用函数库里面调用函数
import {
    Get_Matrix_shape, 
    Get_Zero_Matrix, 
    Judge_str_not_null,
    Matrix_Dot_Multiplication,
    Matrix_Item_Sum,
    Matrix_Multiplication_Number,
    Matrix_Add_Each
} from "./universal-func"

/**
 * @Switch_Train_fun 确定训练函数
 */
export function Switch_Train_fun(Model_index: number, Algorithm_index: number, Parameter: any, Input_X_str: string, Input_Y_str: string){
    // 预判输入变量是否为空
    if(!Judge_str_not_null(Input_X_str, 'Train-X')){
        return
    }
    if(!Judge_str_not_null(Input_Y_str, 'Train-Y')){
        return
    }

    let Return_data: never[] = []
    switch(Model_index){
        case 0:
            Return_data = Perceptron_Original_Train(Parameter, Input_X_str, Input_Y_str)
            break
    }
    return Return_data
}

// 感知机算法:
/**
 * @Perceptron_Original_Train 原始形式
 */
export function Perceptron_Original_Train(Parameter: any[], Input_X_str: string, Input_Y_str: string) {
    let x = eval('(' + Input_X_str + ')')
    let y = eval('(' + Input_Y_str + ')')
    let x_shape = Get_Matrix_shape(x)
    let y_shape = Get_Matrix_shape(y)
    // 初始化 w, b (初始化为0)
    let w = Get_Zero_Matrix(x_shape.slice(1))
    let b = 0
    let n = Parameter[0]["value"]
    // 其它数据
    let index = 0 // 当前的x的子矩阵序号
    let sum = 0 // 当前符合 y(w·x + b) > 0 的x子矩阵的个数
    while(sum < x_shape[0]){
        // 求解 y(w·x + b)
        if(y[index] * (Matrix_Item_Sum(Matrix_Dot_Multiplication(w, x[index])) + b) <= 0){
            w = Matrix_Add_Each(w, Matrix_Multiplication_Number(n * y[index], x[index]))
            b += n * y[index]
            sum = -1
        }

        index = ((index + 1) % x_shape[0])
        sum += 1
    }
    let Return_data: any = [
        {"value": w},
        {"value": b}
    ]
    return Return_data
}