/**
 * 控制台
 */
import React from "react";
import {Row, Col, Select, InputNumber} from "antd"
import "antd/dist/antd.css"
import "../css/control.css"
import $ from "jquery"
// 导入常量
import {CONTROL_MAIN_DAtA} from "../constant/data"
// 导入redux
import {useDispatch} from 'react-redux'
import {Change_Model_index, Change_Algorithm_index, Change_Parameter} from "../../../store/modules/main_redux"

interface ControlProps{
    theme_begin: any,
    // 全局界面宽
    Width_page: number,
}

interface ControlState{
    Model_index: number, // 模型序号
    Algorithm_index: number, // 算法序号
    // 相关属性
    Parameter: {
        name: string,
        value: number
    }[],

    // Element变量
    Option_model_select: {
        value: number,
        label: string
    }[], // model选择器的option
    Option_Algorithm_select: {
        value: number,
        label: string
    }[], // algorithm选择器的option
    Parameter_element: any[], // parameter要显示的element
}

function Control(props: ControlProps, state: ControlState){
    const {theme_begin, Width_page} = props
    const [Model_index, Set_Model_index] = React.useState(0)
    const [Algorithm_index, Set_Algorithm_index] = React.useState(0)
    const [Parameter, Set_Parameter] = React.useState([...JSON.parse(JSON.stringify(CONTROL_MAIN_DAtA["model"][0]["algorithm"][0]["parameter"]))])
    const [Option_model_select, Set_Option_model_select] = React.useState([])
    const [Option_Algorithm_select, Set_Option_Algorithm_select] = React.useState([])
    const [Parameter_element, Set_Parameter_element] = React.useState([])
    let line_height_item = 40 // model选择、algorithm选择 的行高
    let theme = {
        ...theme_begin,
        width: theme_begin.width * Width_page,
        left: theme_begin.left * Width_page
    }
    // redux
    const dispatch = useDispatch()


    React.useEffect(() => {
        let option_model_select = CONTROL_MAIN_DAtA["model"].map((v, index) => {
            return {
                value: index,
                label: v['name']
            }
        })
        let option_algorithm_select = Return_Algorithm_element_item()
        let parameter_element = Parameter.map((v, index) => {
            return Return_Parameter_element_item(index)
        })
        Set_Option_model_select(option_model_select as any)
        Set_Option_Algorithm_select(option_algorithm_select as any)
        Set_Parameter_element(parameter_element as any)
    }, [])

    // 更改model、algorithm以及parameter 
    React.useEffect(() => {
        dispatch(Change_Model_index(Model_index))

        let option_algorithm_select = Return_Algorithm_element_item()
        // 防止因为Algorithm没有进行状态更新而导致后面刷新Parameter没有执行
        Algorithm_index !== 0 ? Set_Algorithm_index(0) : Set_Parameter([...JSON.parse(JSON.stringify(CONTROL_MAIN_DAtA['model'][Model_index]["algorithm"][Algorithm_index]["parameter"]))])
        Set_Option_Algorithm_select(option_algorithm_select as any)
    }, [Model_index])
    React.useEffect(() => {
        dispatch(Change_Algorithm_index(Algorithm_index))

        Set_Parameter([...JSON.parse(JSON.stringify(CONTROL_MAIN_DAtA['model'][Model_index]["algorithm"][Algorithm_index]["parameter"]))])
    }, [Algorithm_index])
    React.useEffect(() => {
        let parameter = [...JSON.parse(JSON.stringify(Parameter))]
        dispatch(Change_Parameter(parameter))

        let parameter_element = Parameter.map((v, index) => {
            return Return_Parameter_element_item(index)
        })
        Set_Parameter_element(parameter_element as any)
    }, [Parameter])

    function stringToHTML(str_: string){
        let div_ = document.createElement('div')
        div_.innerHTML = str_ 
        return div_
    }
    /**
     * @return_Parameter_node 返回参数(parameter)的element div
     */
    function Return_Parameter_element_item(index: number){
        let v = Parameter[index]
        return <div key={CONTROL_MAIN_DAtA['model'][Model_index]['name'] + CONTROL_MAIN_DAtA['model'][Model_index]["algorithm"][Algorithm_index]["name"] + v["name"]}
        style={{float: 'left', width: theme.width - 4 - 7, height: line_height_item}}>
            <div style={{float: 'left', width: 100, height: line_height_item, fontWeight: 600, lineHeight: line_height_item.toString() + 'px', textAlign: 'center'}} dangerouslySetInnerHTML={{__html: v['name'] + ' :'}}></div>
            <InputNumber
                size="small" 
                style={{float: 'left', width: theme.width - 125, height: 25, marginTop: (line_height_item - 25) / 2}}
                value={Parameter[index]['value']}
                step={0.01}
                onChange={(number) => {
                    let parameter = [...Parameter]
                    parameter[index]['value'] = (number as any)
                    Set_Parameter(parameter)
                }}
            ></InputNumber>
        </div>
    }
    /**
     * @Return_Algorithm_element_item 返回算法(algorithm)的element div
     */
    function Return_Algorithm_element_item(){
        return CONTROL_MAIN_DAtA["model"][Model_index]["algorithm"].map((v, index) => {
            return {
                value: index,
                label: v["name"]
            }
        })
    }

    /**
     * @Model_change 模型选择
     */
    function Model_change(index: number){
        Set_Model_index(index)
    }
    /**
     * @Algorithm_change 算法选择
     */
    function Algorithm_change(index: number){
        Set_Algorithm_index(index)
    }


    return(
        <div className="Control" style={{...theme}}>
            {/* model */}
            <div style={{float: 'left', width: theme.width - 2, height: line_height_item, marginTop: (line_height_item - 25) / 2}}>
                <div style={{float: 'left', width: 50, height: line_height_item, textAlign: "center", lineHeight: line_height_item.toString() + "px"}}>&nbsp;Model:&nbsp;</div>
                <Select 
                    size="small" 
                    style={{width: theme.width - 60, height: 25, float: 'left', marginTop: (line_height_item - 25) / 2}}
                    options={Option_model_select}
                    value={Option_model_select.length !== 0 ? (Option_model_select[Model_index] as any).label : ""}
                    onChange={Model_change}
                ></Select>
            </div>
            {/* algorithm */}
            <div style={{float: 'left', width: theme.width - 2, height: line_height_item}}>
                <div style={{float: 'left', width: 70, height: line_height_item, textAlign: "center", lineHeight: line_height_item.toString() + "px"}}>&nbsp;Algorithm:&nbsp;</div>
                <Select 
                    size="small" 
                    style={{width: theme.width - 80, height: 25, float: 'left', marginTop: (line_height_item - 25) / 2}}
                    options={Option_Algorithm_select}
                    value={Option_Algorithm_select.length !== 0 ? (Option_Algorithm_select[Algorithm_index] as any).label : ""}
                    onChange={Algorithm_change}
                ></Select>
            </div>
            <div style={{float: 'left', width: theme.width, height: line_height_item}}>
                <div style={{float: 'left', width: 80, height: line_height_item, textAlign: 'center', lineHeight: line_height_item.toString() + 'px'}}>Parameter</div>
                <div style={{float: 'left', width: theme.width - 90, height: line_height_item / 2, marginTop: line_height_item / 2, borderTop: '2px dashed rgb(180, 180, 180)'}}></div>
            </div>
            {/* parameter */}
            <div id="Parameter-container" style={{float: 'left', width: theme.width - 4, height: theme.height - line_height_item * 3 - 12, overflowY: 'auto', overflowX: 'hidden'}}>
                {Parameter_element}
            </div>
            
        </div>
    )
}

export default Control