import React from "react";
import {PAGE_NAME} from "../constant/data"
// 导入redux
import {useDispatch} from "react-redux"
import {change_Page_now} from "../../store/modules/main_redux"

interface OtherProps{
    Page_now: string, //判断显示哪个页面
}

interface OtherState{
}

function Other(props: OtherProps, state: OtherState){
    const {Page_now} = props
    const dispatch = useDispatch()


    return(
        <div className="Other" style={{display: Page_now === PAGE_NAME[1] ? "inline" : "none"}}>
            <h1 style={{float: 'left', marginLeft: 55, width: "100%"}}>Other</h1>
            <br></br>
            <div style={{width: 150, height: 30, lineHeight: "30px", textAlign: 'center', borderRadius: '20px', float: 'left', marginLeft: 20, background: "rgb(114, 226, 252)", fontWeight: 700, fontSize: "16px", cursor: 'pointer'}}
                onClick={() => {
                    dispatch(change_Page_now(PAGE_NAME[0]))
                }}
            >Codes Page</div>
        </div>
    )
}

export default Other