import React from "react";
import {PAGE_NAME} from "../constant/data"
import {Themes} from "./constant/Theme"
// 子组件
import Title from "./children/title"
import Control from "./children/Control"
import Train from "./children/Train"
import Predict from "./children/Predict"
import Information from "./children/Information"

interface StatisticalProps{
    Page_now: string, // 判断显示哪个页面
}

interface StatisticalState{
}

function Statistical(props: StatisticalProps, state: StatisticalState){
    const {Page_now} = props
    const {TitleTheme, ControlTheme, TrainTheme, PredictTheme, InformationTheme} = Themes
    let Width_page = document.body.clientWidth > 900 ? document.body.clientWidth : 900
    return(
        <div className="Statistical" style={{display: Page_now === PAGE_NAME[0] ? "inline" : "none"}}>
            <Title Width_page={Width_page} theme_begin={TitleTheme}></Title>
            <Control Width_page={Width_page} theme_begin={ControlTheme}></Control>
            <Train Width_page={Width_page} theme_begin={TrainTheme}></Train>
            <Predict Width_page={Width_page} theme_begin={PredictTheme}></Predict>
            <Information Width_page={Width_page} theme_begin={InformationTheme}></Information>
        </div>
    )
}

export default Statistical