import React from "react";
// markdown样式设计
import {marked} from "marked"
import "github-markdown-css"
import hljs from "highlight.js";
import 'highlight.js/styles/atom-one-light.css'
// 导入常量
import {MODEL_NOTE_MARKDOWN} from "../constant/data"
// 导入redux
import {useSelector} from "react-redux"

interface InformationProps{
    theme_begin: any,
    // 全局界面宽
    Width_page: number,
}

interface InformationState{
}

function Information(props: InformationProps, state: InformationState){
    const {theme_begin, Width_page} = props
    let theme = {
        ...theme_begin,
        width: theme_begin.width * Width_page,
        left: theme_begin.left * Width_page
    }
    const {Model_index} = useSelector(state => (state as any).main_redux)


    /**
     * @Get_markDown_data 将markdown格式字符串转成html标签格式
     */
    function Get_markDown_data(){
        return marked(MODEL_NOTE_MARKDOWN['model'][Model_index]['note'])
    }

    React.useEffect(() => {
        let div_ = document.getElementById("Model-Note_show");
        (div_ as any).innerHTML = Get_markDown_data()
        hljs.highlightAll()
    }, [Model_index])


    return(
        <div className="Information" style={{...theme}}>
            <div id="Model-Note_show" className="markdown-body" style={{float: 'left', width: theme.width - 6, height: theme.height - 6, marginLeft: 2, overflow: 'auto'}}></div>
        </div>
    )
}

export default Information