// reducer模块
import {createSlice} from "@reduxjs/toolkit"
// 导入常量
import {PAGE_NAME} from "../../components/constant/data"
import {CONTROL_MAIN_DAtA} from "../../components/statistical_learn/constant/data"

const main_reducer = createSlice({
    name: "mainReducer",
    initialState:{
        Page_now: PAGE_NAME[0], // 当前显示的页面 
        // 当前展示的模型的相关信息
        Model_index: 0,
        Algorithm_index: 0,
        Parameter: CONTROL_MAIN_DAtA["model"][0]['algorithm'][0]["parameter"],
        // 训练模型后传出的数据
        Parameter_Mid: [],
    },
    reducers: {
        // 更改当前显示的页面
        change_Page_now(state, action){
            state.Page_now = action.payload
        },
        /**
         * 当前展示的模型的情况修改
         */
        Change_Model_index(state, action){
            state.Model_index = action.payload
        },
        Change_Algorithm_index(state, action){
            state.Algorithm_index = action.payload
        },
        Change_Parameter(state, action){
            state.Parameter = action.payload
        },
        // 修改当前模型训练的返回数据
        Change_Parameter_Mid(state, action){
            state.Parameter_Mid = action.payload
        }
    }
})

// 导出相关函数
export const {
    change_Page_now, 
    Change_Model_index, 
    Change_Algorithm_index, 
    Change_Parameter,
    Change_Parameter_Mid
} = main_reducer.actions

// 导出reducer，创建store用
export default main_reducer.reducer