// 预测函数

// 从常用函数库里面调用函数
import {
    Judge_str_not_null,
    Get_Matrix_shape,
    Matrix_Item_Sum,
    Matrix_Dot_Multiplication
} from "./universal-func"

/**
 * @Switch_Predict_fun 确定预测函数
 */
export function Switch_Predict_fun(Model_index: number, Algorithm_index: number, Parameter_Mid: any[], Predict_X_str: string){
    // 预判输入变量是否为空
    if(!Judge_str_not_null(Predict_X_str, 'Predict-X')){
        return
    }
    let Return_data = ""
    switch(Model_index){
        case 0:
            Return_data = Perceptron_Original_Predict(Parameter_Mid, Predict_X_str)
            break;
    }
    return Return_data
}


export function Perceptron_Original_Predict(Parameter_Mid: any[], Predict_X_str: string){
    let x = eval('(' + Predict_X_str + ')')
    let x_shape = Get_Matrix_shape(x)
    let w = Parameter_Mid[0]["value"]
    let b = Parameter_Mid[1]["value"]

    let y_list = []
    if(x_shape.length === 1){
        let num = Matrix_Item_Sum(Matrix_Dot_Multiplication(w, x)) + b
        if(num > 0){
            return "1"
        } else if(num < 0){
            return "-1"
        } 
        return "0"
    } else {
        for(let i = 0; i < x_shape[0]; i++){
            console.log(x[i])
            let num = Matrix_Item_Sum(Matrix_Dot_Multiplication(w, x[i])) + b
            if(num > 0){
                y_list.push(1)
            } else if(num < 0){
                y_list.push(-1)
            } else {
                y_list.push(0)
            }
        }
    }
    let y_str = "["
    for(let i = 0; i < y_list.length; i++){
        if(i !== 0){
            y_str += ","
        }
        y_str += " " + y_list[i].toString()
    }
    y_str += ' ]'
    return y_str
}